	module.exports = {"data":{"markdownRemark":{"html":"<h2>npm Cheat Sheet</h2>\n<p>A list of terminal commands and flags to help use <code>npm</code></p>\n<h2>install <code>package.json</code> dependencies</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code>npm install</code></pre>\n      </div>\n<p><strong>Shorthand</strong></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code># install\nnpm i <package>\n# uninstall\nnpm un <package>\n# update\nnpm up <package></code></pre>\n      </div>\n<h2>List globally installed packages.</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code>npm list -g --depth=0</code></pre>\n      </div>\n<h2>Uninstall global package</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code>npm -g uninstall <name> </code></pre>\n      </div>\n<h2>Upgrade NPM on Windows</h2>\n<p>After trying several times to upgrade npm on Windows I found this whilst poking around in the npm folders.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code>npm-windows-upgrade</code></pre>\n      </div>\n<h2>Updating global packages</h2>\n<p>To see which packages need updating, use:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code>npm outdated -g --depth=0</code></pre>\n      </div>\n<p>To update global packages individually you can use:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code>npm update -g <package> <package> <package></code></pre>\n      </div>\n<h2>list available scripts to run</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code>npm run</code></pre>\n      </div>\n<h2>update npm</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code>npm install -g npm@latest\n# using windows? Then use\nnpm-windows-upgrade</code></pre>\n      </div>\n<h2>flags</h2>\n<p><code>-S</code> is the same as <code>--save</code> not needed in npm 5+\n<code>-D</code> is the same as <code>--save-dev</code></p>\n<h2>installed version</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code>npm list # for local packages</code></pre>\n      </div>\n<h2>Node Version Manager <code>nvm</code></h2>\n<p>Say you want to install Node v6.9.1 you would write on the terminal:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code>nvm install 6</code></pre>\n      </div>\n<p>If you have multiple versions of Node.js installed on your workspace, you can switch to a specific version by writing:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code>nvm use 4.8.4</code></pre>\n      </div>\n<h3>Making a node version default.</h3>\n<p>In order to set a default version of node for your workspace, just type:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code>nvm alias default 6</code></pre>\n      </div>\n<p>Where 6 was the version you wanted to be used as default.</p>","fields":{"slug":"/developer-tools/npm-cheatsheet/"},"frontmatter":{"title":"npm Cheat Sheet"}}},"pathContext":{"slug":"/developer-tools/npm-cheatsheet/"}}

